"use client"

import { useState, useEffect } from "react";
import { Plus, MoreHorizontal, X, DollarSign, Calendar, User, Mail, Trash2, Edit2 } from "lucide-react";

// --- Types ---
type OrderStatus = "Pending" | "Processing" | "Completed" | "Cancelled";

interface Order {
    id: string;
    customer: string;
    email: string;
    total: number;
    status: OrderStatus;
    date: string;
    itemsSummary?: string; // Quick summary of what they bought
}

// --- Mock Data Init ---
const INITIAL_ORDERS: Order[] = [
    { id: "ORD-001", customer: "Amrin", email: "amrin@example.com", total: 150.00, status: "Pending", date: "2024-05-15", itemsSummary: "Business Cards (x500)" },
    { id: "ORD-002", customer: "John Doe", email: "john@test.com", total: 85.50, status: "Processing", date: "2024-05-14", itemsSummary: "A5 Flyers" },
];

export default function AdminOrdersPage() {
    const [orders, setOrders] = useState<Order[]>([]);
    const [isLoaded, setIsLoaded] = useState(false);

    // Modal State
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [editingOrder, setEditingOrder] = useState<Order | null>(null);

    // Load from LocalStorage
    useEffect(() => {
        const saved = localStorage.getItem("web2print_orders");
        if (saved) {
            try {
                setOrders(JSON.parse(saved));
            } catch (e) {
                console.error("Failed to parse orders", e);
                setOrders(INITIAL_ORDERS);
            }
        } else {
            setOrders(INITIAL_ORDERS);
        }
        setIsLoaded(true);
    }, []);

    // Save to LocalStorage
    useEffect(() => {
        if (isLoaded) {
            localStorage.setItem("web2print_orders", JSON.stringify(orders));
        }
    }, [orders, isLoaded]);

    const handleSaveOrder = (order: Order) => {
        if (editingOrder) {
            // Update existing
            setOrders(prev => prev.map(o => o.id === order.id ? order : o));
        } else {
            // Create new
            setOrders(prev => [...prev, order]);
        }
        setIsModalOpen(false);
        setEditingOrder(null);
    };

    const handleDeleteOrder = (id: string) => {
        if (confirm("Are you sure you want to delete this order?")) {
            setOrders(prev => prev.filter(o => o.id !== id));
        }
    };

    const openNewOrder = () => {
        setEditingOrder(null);
        setIsModalOpen(true);
    };

    const openEditOrder = (order: Order) => {
        setEditingOrder(order);
        setIsModalOpen(true);
    };

    const updateStatus = (id: string, newStatus: OrderStatus) => {
        setOrders(prev => prev.map(o => o.id === id ? { ...o, status: newStatus } : o));
    };

    if (!isLoaded) return <div className="p-8">Loading orders...</div>;

    return (
        <div className="space-y-6 h-full flex flex-col">
            <div className="flex justify-between items-center">
                <div>
                    <h2 className="text-2xl font-bold text-gray-800">Order Kanban</h2>
                    <p className="text-gray-500">Dragless Kanban Board for Order Management</p>
                </div>
                <button
                    onClick={openNewOrder}
                    className="flex items-center gap-2 bg-black text-white px-4 py-2 rounded-lg hover:bg-gray-800 transition-colors font-medium text-sm"
                >
                    <Plus className="h-4 w-4" /> New Order
                </button>
            </div>

            {/* Kanban Board */}
            <div className="flex-1 overflow-x-auto pb-4">
                <div className="flex gap-6 min-w-[1000px] h-full">
                    <KanbanColumn
                        title="Pending"
                        status="Pending"
                        orders={orders.filter(o => o.status === "Pending")}
                        onEdit={openEditOrder}
                        onDelete={handleDeleteOrder}
                        onStatusChange={updateStatus}
                        colorClass="bg-yellow-50 border-yellow-100 text-yellow-900"
                        dotClass="bg-yellow-400"
                    />
                    <KanbanColumn
                        title="Processing"
                        status="Processing"
                        orders={orders.filter(o => o.status === "Processing")}
                        onEdit={openEditOrder}
                        onDelete={handleDeleteOrder}
                        onStatusChange={updateStatus}
                        colorClass="bg-blue-50 border-blue-100 text-blue-900"
                        dotClass="bg-blue-400"
                    />
                    <KanbanColumn
                        title="Completed"
                        status="Completed"
                        orders={orders.filter(o => o.status === "Completed")}
                        onEdit={openEditOrder}
                        onDelete={handleDeleteOrder}
                        onStatusChange={updateStatus}
                        colorClass="bg-green-50 border-green-100 text-green-900"
                        dotClass="bg-green-400"
                    />
                    <KanbanColumn
                        title="Cancelled"
                        status="Cancelled"
                        orders={orders.filter(o => o.status === "Cancelled")}
                        onEdit={openEditOrder}
                        onDelete={handleDeleteOrder}
                        onStatusChange={updateStatus}
                        colorClass="bg-red-50 border-red-100 text-red-900"
                        dotClass="bg-red-400"
                    />
                </div>
            </div>

            {/* Modal */}
            {isModalOpen && (
                <OrderModal
                    initialData={editingOrder}
                    onClose={() => setIsModalOpen(false)}
                    onSave={handleSaveOrder}
                />
            )}
        </div>
    );
}

// --- Subcomponents ---

function KanbanColumn({ title, status, orders, onEdit, onDelete, onStatusChange, colorClass, dotClass }: any) {
    return (
        <div className={`flex-1 rounded-xl border p-4 flex flex-col gap-4 ${colorClass} bg-opacity-50`}>
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 font-bold">
                    <div className={`w-2 h-2 rounded-full ${dotClass}`} />
                    {title}
                    <span className="bg-white/50 px-2 py-0.5 rounded text-xs ml-1 border border-black/5">
                        {orders.length}
                    </span>
                </div>
            </div>

            <div className="flex-1 space-y-3 overflow-y-auto min-h-[500px]">
                {orders.map((order: Order) => (
                    <OrderCard
                        key={order.id}
                        order={order}
                        onEdit={onEdit}
                        onDelete={onDelete}
                        onStatusChange={onStatusChange}
                    />
                ))}
                {orders.length === 0 && (
                    <div className="h-24 border-2 border-dashed border-black/5 rounded-lg flex items-center justify-center text-sm opacity-50">
                        No orders
                    </div>
                )}
            </div>
        </div>
    );
}

function OrderCard({ order, onEdit, onDelete, onStatusChange }: {
    order: Order,
    onEdit: (o: Order) => void,
    onDelete: (id: string) => void,
    onStatusChange: (id: string, s: OrderStatus) => void
}) {
    return (
        <div className="bg-white p-4 rounded-lg shadow-sm border border-black/5 hover:shadow-md transition-shadow group">
            <div className="flex justify-between items-start mb-2">
                <span className="text-xs font-mono text-gray-400 bg-gray-50 px-1.5 py-0.5 rounded">{order.id}</span>
                <div className="flex gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                    <button onClick={() => onEdit(order)} className="p-1 hover:bg-gray-100 rounded text-gray-500 hover:text-blue-600">
                        <Edit2 className="h-3.5 w-3.5" />
                    </button>
                    <button onClick={() => onDelete(order.id)} className="p-1 hover:bg-gray-100 rounded text-gray-500 hover:text-red-600">
                        <Trash2 className="h-3.5 w-3.5" />
                    </button>
                </div>
            </div>

            <div className="mb-3">
                <h4 className="font-bold text-gray-900 text-sm">{order.customer}</h4>
                <p className="text-xs text-gray-500">{order.itemsSummary || "No items summary"}</p>
            </div>

            <div className="flex items-center justify-between mt-3 pt-3 border-t border-gray-50">
                <span className="font-bold text-sm text-gray-900">RM{Number(order.total).toFixed(2)}</span>

                <select
                    value={order.status}
                    onChange={(e) => onStatusChange(order.id, e.target.value as OrderStatus)}
                    className="text-xs border rounded px-1.5 py-1 bg-gray-50 hover:bg-gray-100 outline-none cursor-pointer"
                >
                    <option value="Pending">Pending</option>
                    <option value="Processing">Processing</option>
                    <option value="Completed">Completed</option>
                    <option value="Cancelled">Cancelled</option>
                </select>
            </div>
        </div>
    );
}

function OrderModal({ initialData, onClose, onSave }: { initialData: Order | null, onClose: () => void, onSave: (o: Order) => void }) {
    const [formData, setFormData] = useState<Partial<Order>>({
        customer: "",
        email: "",
        total: 0,
        status: "Pending",
        itemsSummary: "",
        ...initialData
    });

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        onSave({
            ...formData,
            id: initialData?.id || `ORD-${Math.floor(Math.random() * 10000)}`,
            date: initialData?.date || new Date().toISOString().split('T')[0],
        } as Order);
    };

    return (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
            <div className="bg-white rounded-xl shadow-xl w-full max-w-md overflow-hidden animate-in fade-in zoom-in-95 duration-200">
                <div className="px-6 py-4 border-b flex justify-between items-center bg-gray-50">
                    <h3 className="font-bold text-lg">{initialData ? "Edit Order" : "New Order"}</h3>
                    <button onClick={onClose} className="text-gray-400 hover:text-gray-900">
                        <X className="h-5 w-5" />
                    </button>
                </div>

                <form onSubmit={handleSubmit} className="p-6 space-y-4">
                    <div className="space-y-2">
                        <label className="text-sm font-medium text-gray-700">Customer Name</label>
                        <div className="relative">
                            <User className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                            <input
                                required
                                className="w-full pl-9 pr-3 py-2 border rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all"
                                placeholder="John Doe"
                                value={formData.customer}
                                onChange={e => setFormData({ ...formData, customer: e.target.value })}
                            />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm font-medium text-gray-700">Email</label>
                        <div className="relative">
                            <Mail className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                            <input
                                type="email"
                                className="w-full pl-9 pr-3 py-2 border rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-all"
                                placeholder="john@example.com"
                                value={formData.email}
                                onChange={e => setFormData({ ...formData, email: e.target.value })}
                            />
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <label className="text-sm font-medium text-gray-700">Total (RM)</label>
                            <div className="relative">
                                <DollarSign className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                                <input
                                    type="number"
                                    step="0.01"
                                    required
                                    className="w-full pl-9 pr-3 py-2 border rounded-lg text-sm focus:ring-2 focus:ring-blue-500 outline-none"
                                    value={formData.total}
                                    onChange={e => setFormData({ ...formData, total: parseFloat(e.target.value) })}
                                />
                            </div>
                        </div>

                        <div className="space-y-2">
                            <label className="text-sm font-medium text-gray-700">Status</label>
                            <select
                                className="w-full px-3 py-2 border rounded-lg text-sm focus:ring-2 focus:ring-blue-500 outline-none"
                                value={formData.status}
                                onChange={e => setFormData({ ...formData, status: e.target.value as OrderStatus })}
                            >
                                <option value="Pending">Pending</option>
                                <option value="Processing">Processing</option>
                                <option value="Completed">Completed</option>
                                <option value="Cancelled">Cancelled</option>
                            </select>
                        </div>
                    </div>

                    <div className="space-y-2">
                        <label className="text-sm font-medium text-gray-700">Items Summary</label>
                        <textarea
                            className="w-full px-3 py-2 border rounded-lg text-sm focus:ring-2 focus:ring-blue-500 outline-none min-h-[80px]"
                            placeholder="e.g. 500x Business Cards, 100x Flyers..."
                            value={formData.itemsSummary}
                            onChange={e => setFormData({ ...formData, itemsSummary: e.target.value })}
                        />
                    </div>

                    <div className="pt-4 flex gap-3">
                        <button
                            type="button"
                            onClick={onClose}
                            className="flex-1 py-2.5 border rounded-lg text-sm font-medium hover:bg-gray-50 transition-colors"
                        >
                            Cancel
                        </button>
                        <button
                            type="submit"
                            className="flex-1 py-2.5 bg-black text-white rounded-lg text-sm font-medium hover:bg-gray-800 transition-colors"
                        >
                            {initialData ? "Update Order" : "Create Order"}
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
}
